import React from "react";
import "./style.css";
import { CardWithCode, Title } from "components";

export const Home = () => {
  const codeOne = `class ShoppingList extends React.Component {
    render() {
      return (
        <div className="shopping-list">
          <h1>   {this.props.name}</h1>
          <ul>
            <li>Instagram</li>
            <li>WhatsApp</li>
            <li>Oculus</li>
          </ul>
        </div>
      )
    }
  }
  
  //  : <ShoppingList name="" />`;
  const codeTwo = `return React.createElement(
    'div',
    { className: 'shopping-list' },
    React.createElement('h1' /* ... h1 children ... */),
    React.createElement('ul' /* ... ul children ... */)
  )`;

  return (
    <div>
      <Title title="" />
      <p>
        React   ,    JavaScript 
           .
      </p>
      <p>
             UI   
         ,  .
      </p>
      <p>
        React     ,    
         React.Component:
      </p>
      <CardWithCode codeString={codeOne} />
      <p>ShoppingList     React.</p>
      <p>
          ,    (props,
          properties  ),     render()
           .
      </p>
      <p>
         render   ,      .
        React      .
      </p>
      <p>
         , render  React-,  
          ,   .
      </p>
      <p>
         React-    
         JSX    .
      </p>
      <p>
            div  
        React.createElement('div').     :
      </p>
      <CardWithCode codeString={codeTwo} />
      <p>     </p>
    </div>
  );
};
