import { CardWithCode, Title } from "components";
import React from "react";

export const Events = () => {
  const codeTwo = ` function Form() {
    function handleSubmit(e) {
      e.preventDefault();
      console.log(' .');
    }
    return (
      <form onSubmit={handleSubmit}>
        <button type="submit"></button>
      </form>
    );
}`;

  return (
    <div>
      <Title title=" " />
      <p>
          React    camelCase   .
      </p>
      <p> JSX        .</p>
      <p>  : onClick, onChange</p>
      <p>   ,   :</p>
      <ul>
        <li>
          <p> : onCopy, onCut, onPaste</p>
        </li>
        <li>
          <p>
            / : onCompositionEnd, onCompositionStart,
            onCompositionUpdate
          </p>
        </li>
        <li>
          <p>: onKeyDown, onKeyPress, onKeyUp</p>
        </li>
        <li>
          <p>: onFocus, onBlur</p>
        </li>
        <li>
          <p>: onChange, onInput, onSubmit</p>
        </li>
        <li>
          <p>: onSelect</p>
        </li>
        <li>
          <p>: onTouchCancel, onTouchEnd, onTouchMove, onTouchStart</p>
        </li>
        <li>
          <p>UI: onScroll</p>
        </li>
        <li>
          <p> : onWheel</p>
        </li>
        <li>
          <p>: onLoad, onError</p>
        </li>
        <li>
          <p>
            : onAnimationStart, onAnimationEnd, onAnimationIteration
          </p>
        </li>
        <li>
          <p>: onTransitionEnd</p>
        </li>
      </ul>
      <p>
         React      , 
        false.    preventDefault.
      </p>
      <CardWithCode codeString={codeTwo} />
    </div>
  );
};
